import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { Formik } from 'formik';

import api from '../services/api';
import { setToken } from '../services/authentication';
import signInValidation from '../validations/signInValidation';
import Form from '../components/Forms/Form';
import Field from '../components/Forms/Field';
import Carousel from '../components/Carousel';
import logo from '../images/bitzer-logo-branco.png';
import cover from '../images/login/3.png';
import bitzerDashboard1 from '../images/login/bitzer-dashboard.png';
import bitzerDashboard2 from '../images/login/bitzer-dashboard2.png';
import bitzerDashboard3 from '../images/login/bitzer-dashboard3.png';

export default class SignIn extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
    };
    this.onSubmit = this.onSubmit.bind(this);
  }

  onSubmit(values) {
    const { history } = this.props;

    this.setState({ isLoading: true });

    api.post('/token', values)
      .then((response) => {
        setToken(response.data);
        history.push('/');
      })
      .catch((error) => {
        console.error(error);
        this.setState({ isLoading: false });
      });
  }

  render() {
    const { isLoading } = this.state;

    return (
      <section className="sign-in-page bg-white">
        <div className="container-fluid p-0">
          <div className="row no-gutters">
            <div className="col-sm-6 align-self-center">
              <div className="sign-in-from">
                <h1 className="mb-0">Acesso</h1>
                <p>Digite seu endereço de e-mail e senha para acessar o painel de administração.</p>
                <Formik
                  initialValues={{
                    email: '',
                    password: '',
                  }}
                  validationSchema={signInValidation}
                  onSubmit={this.onSubmit}
                >
                  {({ errors, touched }) => (
                    <Form className="mt-4">
                      <Field
                        name="email"
                        type="email"
                        label="Endereço de email"
                        className="form-control mb-0"
                        placeholder="Email"
                        errors={errors.email}
                        touched={touched.email}
                      />
                      <Field
                        name="password"
                        type="password"
                        label="Senha"
                        className="form-control mb-0"
                        placeholder="Senha"
                        errors={errors.password}
                        touched={touched.password}
                        labelHelp={<Link to="/">Esqueceu sua senha?</Link>}
                      />
                      <div className="text-right">
                        <button
                          type="submit"
                          className="btn btn-dark"
                          disabled={isLoading}
                        >
                          <b>Entrar</b>
                        </button>
                      </div>
                    </Form>
                  )}
                </Formik>
                <div className="sign-info">
                  <span className="dark-color d-inline-block line-height-2">
                    Acesso ao admin&nbsp;
                    <Link to="/">Clique aqui</Link>
                  </span>
                </div>
              </div>
            </div>
            <div className="col-sm-6 text-center">
              <div
                className="sign-in-detail text-white"
                style={{
                  background: `url(${cover}) no-repeat 0 0`,
                  backgroundSize: 'cover',
                }}
              >
                <span className="sign-in-logo mb-5">
                  <img
                    src={logo}
                    className="img-fluid"
                    alt="logo"
                  />
                </span>
                <Carousel
                  items={[{
                    image: bitzerDashboard1,
                    title: 'Soluções Eficientes para suas Aplicações',
                    text: 'Os desafios da indústria de refrigeração e climatização são múltiplos, de forma que as soluções também o devem ser.',
                  }, {
                    image: bitzerDashboard2,
                    title: 'Soluções Eficientes para suas Aplicações',
                    text: 'Os desafios da indústria de refrigeração e climatização são múltiplos, de forma que as soluções também o devem ser.',
                  }, {
                    image: bitzerDashboard3,
                    title: 'Soluções Eficientes para suas Aplicações',
                    text: 'Os desafios da indústria de refrigeração e climatização são múltiplos, de forma que as soluções também o devem ser.',
                  }]}
                />
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

SignIn.propTypes = {
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
};
